@import 'variables/_globals.scss';

@import 'variables/_buttons.scss';

// Start Theme

// Input Label (<label>)

$input-label: () !default;
$input-label: map-deep-merge((
	color: $black,
	font-size: 1rem,
	font-weight: $font-weight-bold,
	margin-bottom: 0.25rem,
	margin-left: calc(1rem + 1px),
	for: (
		cursor: $link-cursor,
	),
	focus: (
		color: null,
	),
	disabled: (
		color: $gray-600,
		cursor: $disabled-cursor,
	),
	reference-mark: (
		color: $red,
	)
), $input-label);

// Dropdown

// .dropdown-toggle

$dropdown-toggle: () !default;
$dropdown-toggle: map-deep-merge(
	(
		white-space: nowrap,
	),
	$dropdown-toggle
);

// .dropdown-menu

$dialect-dropdown-arrow-border-color: darken($gray-300, 10);
$dialect-dropdown-arrow-border-width: 1px;

$dialect-dropdown-arrow: clay-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1030 638"><path d="M1017 570L541 12Q530 0 515 0t-26 12L13 570q-16 19-7 43.5T39 638h952q24 0 33-24.5t-7-43.5z" fill="#{$white}"></path></svg>');
$dialect-dropdown-arrow-border: clay-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1030 638"><path d="M1017 570L541 12Q530 0 515 0t-26 12L13 570q-16 19-7 43.5T39 638h952q24 0 33-24.5t-7-43.5z" fill="#{$dialect-dropdown-arrow-border-color}"></path></svg>');

$dialect-dropdown-arrow-height: 5px;
$dialect-dropdown-arrow-width: $dialect-dropdown-arrow-height * 2;
$dialect-dropdown-arrow-offset: 12px;

$dropdown-padding-y: 0 !default;
$dropdown-spacer: 0.625rem !default;

$dropdown-menu: () !default;
$dropdown-menu: map-deep-merge(
	(
		background-color: $white,
		border-color: $gray-300,
		border-radius: 0.25rem,
		border-width: 1px,
		box-shadow: $c-unset,
		font-size: 0.8125rem,
		margin: $dropdown-spacer 0 0,
		max-height: $c-unset,
		max-width: 240px,
		min-height: 40px,
		min-width: 240px,
		overflow: visible,
		padding: $dropdown-padding-y 0,
		before: (
			background-image: #{$dialect-dropdown-arrow, $dialect-dropdown-arrow-border},
			background-position: #{0 $dialect-dropdown-arrow-border-width, 0 0},
			background-repeat: no-repeat,
			background-size: 100% auto,
			content: '',
			height: $dialect-dropdown-arrow-height,
			left: $dialect-dropdown-arrow-offset,
			position: absolute,
			top: math-sign($dialect-dropdown-arrow-height),
			width: $dialect-dropdown-arrow-width,
		),
	),
	$dropdown-menu
);

// .dropdown-item, 

$dropdown-item-padding-x: 1rem !default;
$dropdown-item-padding-y: 0.65625rem !default;

// Weird box-shadow inset with border-radius render in IE https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/12515080/

$dropdown-item-base: () !default;
$dropdown-item-base: map-deep-merge(
	(
		background-color: transparent,
		border-radius: 0,
		border-width: 0,
		clear: both,
		color: $gray-800,
		cursor: $link-cursor,
		display: block,
		font-size: inherit,
		font-weight: $font-weight-bold,
		overflow: hidden,
		padding-bottom: $dropdown-item-padding-y,
		padding-left: $dropdown-item-padding-x,
		padding-right: $dropdown-item-padding-x,
		padding-top: $dropdown-item-padding-y,
		position: relative,
		text-align: inherit,
		transition: none,
		white-space: normal,
		width: 100%,
		word-wrap: break-word,
		hover: (
			background-color: clay-lighten($component-active-bg, 44.9),
			color: $gray-900,
		),
		focus: (
			box-shadow: $component-focus-inset-box-shadow,
			outline: 0,
			text-decoration: none,
		),
		active: (
			background-color: $primary-l2,
			color: $primary-d2,
			font-weight: $font-weight-semi-bold,
		),
		active-class: (
			c-kbd-inline: (
				color: inherit,
			),
		),
		disabled: (
			color: $gray-500,
			box-shadow: none,
		),
		c-kbd-inline: (
			color: $gray-500,
		),
	),
	$dropdown-item-base
);

// .dropdown-header

$dropdown-header: () !default;
$dropdown-header: map-deep-merge(
	(
		breakpoint-down: sm,
		color: $gray-700,
		display: block,
		font-size: 0.875rem,
		margin-top: 0.625rem,
		padding: 0.46875rem $dropdown-item-padding-x,
		padding-bottom: $c-unset,
		padding-left: $c-unset,
		padding-right: $c-unset,
		padding-top: $c-unset,
		position: relative,
		text-transform: uppercase,
		word-wrap: break-word,
		first-child: (
			margin-top: 0,
		),
	),
	$dropdown-header
);

// .dropdown-subheader

$dropdown-subheader: () !default;
$dropdown-subheader: map-deep-merge(
	(
		color: $gray-700,
		font-size: 0.875rem,
		font-weight: $c-unset,
		margin-top: $c-unset,
		padding: map-get($dropdown-header, padding),
		padding-bottom: $c-unset,
		padding-left: $c-unset,
		padding-right: $c-unset,
		padding-top: $c-unset,
		text-transform: uppercase,
		white-space: normal,
		word-wrap: break-word,
		first-child: (
			margin-top: 0,
		),
	),
	$dropdown-subheader
);

// .dropdown-caption

$dropdown-caption: () !default;
$dropdown-caption: map-merge(
	(
		color: $gray-600,
		font-size: 0.875rem,
		font-weight: $c-unset,
		padding: map-get($dropdown-header, padding),
		white-space: normal,
		word-wrap: break-word,
	),
	$dropdown-caption
);

// .dropdown-divider

$dropdown-divider: () !default;
$dropdown-divider: map-merge(
	(
		background-color: $c-unset,
		border-top: 1px solid $gray-300,
		height: 0,
		margin: 0,
		overflow: hidden,
	),
	$dropdown-divider
);

// .dropdown-section

$dropdown-section: () !default;
$dropdown-section: map-deep-merge(
	(
		padding: 0.375rem 1rem,
		form-group: (
			form-group: (
				margin-top: $dropdown-item-padding-y * 2,
			),
		),
		custom-control: (
			margin-bottom: 0,
		),
		custom-control-label: (
			color: $c-unset,
		),
		custom-control-label-text: (
			padding-left: $c-unset,
		),
		active: (
			custom-control-label: (
				color: $c-unset,
			),
		),
	),
	$dropdown-section
);

// Autocomplete Dropdown Menu

$autocomplete-dropdown-menu: () !default;
$autocomplete-dropdown-menu: map-deep-merge(
	(
		max-height: $c-unset,
	),
	$autocomplete-dropdown-menu
);

// .dropdown-menu-top

$dropdown-menu-top: () !default;
$dropdown-menu-top: map-deep-merge(
	(
		bottom: 100% !important,
		left: 0 !important,
		margin-top: 0,
		margin-bottom: $dialect-dropdown-arrow-height,
		right: auto !important,
		top: auto !important,
		transform: none !important,
		will-change: auto !important,
		before: (
			bottom: math-sign($dialect-dropdown-arrow-height),
			top: auto,
			transform: rotate(180deg),
		),
	),
	$dropdown-menu-top
);

// .dropdown-menu-top-right

$dropdown-menu-top-right: () !default;
$dropdown-menu-top-right: map-deep-merge(
	(
		bottom: 100% !important,
		left: auto !important,
		margin-top: 0,
		margin-bottom: $dialect-dropdown-arrow-height,
		right: 0 !important,
		top: auto !important,
		transform: none !important,
		will-change: auto !important,
		before: (
			bottom: math-sign($dialect-dropdown-arrow-height),
			left: auto,
			right: $dialect-dropdown-arrow-offset,
			top: auto,
			transform: rotate(180deg),
		),
	),
	$dropdown-menu-top-right
);

// .dropdown-menu-top-center

$dropdown-menu-top-center: () !default;
$dropdown-menu-top-center: map-deep-merge(
	(
		bottom: 100% !important,
		left: 50% !important,
		margin-top: 0,
		margin-bottom: $dialect-dropdown-arrow-height,
		right: auto !important,
		top: auto !important,
		transform: translateX(-50%) !important,
		will-change: auto !important,
		before: (
			bottom: math-sign($dialect-dropdown-arrow-height),
			left: 50%,
			margin-left: math-sign($dialect-dropdown-arrow-height),
			top: auto,
			transform: rotate(180deg),
		),
	),
	$dropdown-menu-top-center
);

// .dropdown-menu-center

$dropdown-menu-center: () !default;
$dropdown-menu-center: map-deep-merge(
	(
		bottom: auto !important,
		left: 50% !important,
		right: auto !important,
		top: 100% !important,
		transform: translateX(-50%) !important,
		will-change: auto !important,
		before: (
			left: 50%,
			margin-left: math-sign($dialect-dropdown-arrow-width * 0.5),
		),
	),
	$dropdown-menu-center
);

// .dropdown-menu-left-side

$dropdown-menu-left-side: () !default;
$dropdown-menu-left-side: map-deep-merge(
	(
		bottom: auto !important,
		left: auto !important,
		margin-right: $dialect-dropdown-arrow-height,
		margin-top: 0,
		right: 100% !important,
		top: 0 !important,
		transform: none !important,
		will-change: auto !important,
		before: (
			left: auto,
			right: -7px,
			top: $dialect-dropdown-arrow-offset,
			transform: rotate(90deg),
		),
	),
	$dropdown-menu-left-side
);

// .dropdown-menu-left-side-bottom

$dropdown-menu-left-side-bottom: () !default;
$dropdown-menu-left-side-bottom: map-deep-merge(
	(
		bottom: 0 !important,
		left: auto !important,
		margin-right: $dialect-dropdown-arrow-height,
		margin-top: 0,
		right: 100% !important,
		top: auto !important,
		transform: none !important,
		will-change: auto !important,
		before: (
			bottom: $dialect-dropdown-arrow-offset,
			left: auto,
			right: -7px,
			top: auto,
			transform: rotate(90deg),
		),
	),
	$dropdown-menu-left-side-bottom
);

// .dropdown-menu-left-side-middle

$dropdown-menu-left-side-middle: () !default;
$dropdown-menu-left-side-middle: map-deep-merge(
	(
		bottom: auto !important,
		left: auto !important,
		margin-right: $dialect-dropdown-arrow-height,
		margin-top: 0,
		right: 100% !important,
		top: 50% !important,
		transform: translate(0, -50%) !important,
		will-change: auto !important,
		before: (
			left: auto,
			margin-top: math-sign($dialect-dropdown-arrow-width * 0.5),
			right: -7px,
			top: 50%,
			transform: rotate(90deg),
		),
	),
	$dropdown-menu-left-side-middle
);

// .dropdown-menu-right-side

$dropdown-menu-right-side: () !default;
$dropdown-menu-right-side: map-deep-merge(
	(
		bottom: auto !important,
		left: 100% !important,
		margin-left: $dialect-dropdown-arrow-height,
		margin-top: 0,
		right: auto !important,
		top: 0 !important,
		transform: none !important,
		will-change: auto !important,
		before: (
			left: -7px,
			top: $dialect-dropdown-arrow-offset,
			transform: rotate(-90deg),
		),
	),
	$dropdown-menu-right-side
);

// .dropdown-menu-right-side-bottom

$dropdown-menu-right-side-bottom: () !default;
$dropdown-menu-right-side-bottom: map-deep-merge(
	(
		bottom: 0 !important,
		left: 100% !important,
		margin-left: $dialect-dropdown-arrow-height,
		margin-top: 0,
		right: auto !important,
		top: auto !important,
		transform: none !important,
		will-change: auto !important,
		before: (
			bottom: $dialect-dropdown-arrow-offset,
			left: -7px,
			top: auto,
			transform: rotate(-90deg),
		),
	),
	$dropdown-menu-right-side-bottom
);

// .dropdown-menu-right-side-middle

$dropdown-menu-right-side-middle: () !default;
$dropdown-menu-right-side-middle: map-deep-merge(
	(
		bottom: auto !important,
		left: 100% !important,
		margin-left: $dialect-dropdown-arrow-height,
		margin-top: 0,
		right: auto !important,
		top: 50% !important,
		transform: translate(0, -50%) !important,
		will-change: auto !important,
		before: (
			left: -7px,
			margin-top: math-sign($dialect-dropdown-arrow-width * 0.5),
			top: 50%,
			transform: rotate(-90deg),
		),
	),
	$dropdown-menu-right-side-middle
);

// Dropdown Action

$dropdown-action-toggle-font-size: 1rem !default; // 16px
$dropdown-action-toggle-size: 2.25rem !default;

$dropdown-action-toggle-disabled-opacity: 1 !default;

// .drilldown.dropdown-menu

$drilldown-dropdown-menu: () !default;
$drilldown-dropdown-menu: map-merge(
	(
		overflow: visible,
	),
	$drilldown-dropdown-menu
);

// .drilldown .dropdown-item-indicator-start

$drilldown-dropdown-item-indicator-start: () !default;
$drilldown-dropdown-item-indicator-start: map-merge(
	(
		top: 0.375rem,
	),
	$drilldown-dropdown-item-indicator-start
);

// .drilldown .dropdown-item-indicator-end

$drilldown-dropdown-item-indicator-end: () !default;
$drilldown-dropdown-item-indicator-end: map-merge(
	(
		top: 0.375rem,
	),
	$drilldown-dropdown-item-indicator-end
);

// .drilldown .dropdown-header

$drilldown-dropdown-header: () !default;
$drilldown-dropdown-header: map-merge(
	(
		color: $c-unset,
		font-weight: $c-unset,
		padding-bottom: $c-unset,
		padding-left: $c-unset,
		padding-right: $c-unset,
		padding-top: $c-unset,
		text-transform: $c-unset,
	),
	$drilldown-dropdown-header
);

// Input

$input-border-width: 0.0625rem !default; // 1px

$input-border-bottom-width: $input-border-width !default; // 1px
$input-border-left-width: $input-border-width !default; // 1px
$input-border-right-width: $input-border-width !default; // 1px
$input-border-top-width: $input-border-width !default; // 1px

$input-height: 3rem !default;

$input: () !default;
$input: map-deep-merge((
	background-color: $white,
	border-color: $gray-400,
	border-style: solid,
	border-width: $input-border-width,
	border-radius: 8px,
	box-shadow: none,
	color: $gray-400,
	font-size: 1rem,
	font-weight: $font-weight-bold,
	height: $input-height,
	line-height: $line-height-base,
	padding-bottom: 0.75rem,
	padding-left: 1rem,
	padding-right: 1rem,
	padding-top: 0.75rem,
	transition: #{border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out},
	placeholder: (
		color: rgba($black, 0.4),
	),
	focus: (
		background-color: $c-unset,
		border-color: $component-active-bg,
		box-shadow: none,
		color: $black,
	),
	disabled: (
		background-color: $c-unset,
		border-color: $black,
		color: $black,
		cursor: $disabled-cursor,
		opacity: 0.4,
	),
),$input);

$input-readonly: () !default;
$input-readonly: map-deep-merge((
	font-weight: $font-weight-bold,
),$input-readonly);

// Select

$input-select: () !default;
$input-select: map-deep-merge((
	background-image: clay-icon(caret-bottom, $gray-500),
	background-position: right 12px center,
	background-size: 20px 20px,
	color: $black,
	focus: (
		background-image: null,
	),
	disabled: (
		background-image: clay-icon(caret-bottom, $gray-400),
	),
),$input-select);

// Input Lg

$input-height-lg: 3.5rem !default;

// Input Sm

$input-height-sm: 2.25rem !default;

// Input Danger

$input-danger-focus-box-shadow: none !default;
$input-danger-select-icon: clay-icon(caret-bottom, $gray-500) !default;

// Input Success

$input-success-focus-box-shadow: none !default;
$input-success-select-icon: clay-icon(caret-bottom, $gray-500) !default;

// Input Warning

$input-warning-focus-box-shadow: none !default;
$input-warning-select-icon: clay-icon(caret-bottom, $gray-500) !default;

// .form-legend

$form-legend: () !default;
$form-legend: map-merge((
	color: $black,
	font-size: 1rem,
	font-weight: 800,
	margin-bottom: 0.625rem,
), $form-legend);

// Custom Form

$custom-forms-transition: background-color 0.15s ease-in-out,
	border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out, transform 0.15s ease-in-out !default;

// Custom Control Indicator

$custom-control-indicator-size: 1rem !default;

$custom-control-indicator-position-top: 0.25rem !default;

// .custom-control

$custom-control-gutter: 0.5rem !default;
$custom-control-spacer-x: 1rem !default;

$custom-control: () !default;
$custom-control: map-deep-merge(
	(
		cursor: $c-unset,
		display: block,
		line-height: 1,
		margin-bottom: 0.5rem,
		margin-top: $c-unset,
		min-height:  $custom-control-indicator-size + ($custom-control-indicator-position-top * 2),
		position: relative,
		text-align: left,
		label: (
			display: inline-block,
			margin-left: 0,
			position: relative,
		),
	),
	$custom-control
);

// .custom-control-label

$custom-control-label: () !default;
$custom-control-label: map-deep-merge(
	(
		color: $c-unset,
		cursor: pointer,
		font-size: 0.8125rem,
		font-weight: $font-weight-bold,
		line-height: map-get($custom-control, min-height),
		margin-bottom: 0,
		position: static,
		vertical-align: top,
		before: (
			background-color: $white,
			border-color: $primary,
			border-style: solid,
			border-width: 0.0625rem,
			box-shadow: $c-unset,
			content: '',
			display: block,
			float: left,
			font-size: $custom-control-indicator-size,
			height: $custom-control-indicator-size,
			left: 0,
			position: relative,
			top: $custom-control-indicator-position-top,
			transition: clay-enable-transitions($custom-forms-transition),
			width: $custom-control-indicator-size,
			z-index: 2,
		),
		after: (
			background: no-repeat 50% / 50% 50%,
			border-radius: 100px,
			content: '',
			display: block,
			height: $custom-control-indicator-size,
			left: 0,
			position: absolute,
			top: $custom-control-indicator-position-top,
			transition: $custom-forms-transition,
			width: $custom-control-indicator-size,
			z-index: 1,
		),
	),
	$custom-control-label
);

$custom-control-label-text: () !default;
$custom-control-label-text: map-deep-merge(
	(
		padding-left: 0.75rem,
	),
	$custom-control-label-text
);

$custom-control-label-text-small: () !default;
$custom-control-label-text-small: map-deep-merge(
	(
		font-size: 100%,
	),
	$custom-control-label-text-small
);

// .custom-control-input

$custom-control-input: () !default;
$custom-control-input: map-deep-merge(
	(
		cursor: $link-cursor,
		height: 1.5rem,
		left: 0,
		opacity: 0,
		position: absolute,
		top: 0,
		width: 100%,
		z-index: 3,
		hover: (
			custom-control-label: (
				before: (
					border-color: $primary-d1,
				),
				after: (
 					background-color: rgba($primary, 0.07),
 					transform: scale(2.5),
				),
			),
		),
		focus: (
			custom-control-label: (
				before: (
					border-color: $primary-d1,
					box-shadow: $c-unset,
				),
				after: (
					background-color: rgba($primary, 0.07),
					transform: scale(2.5),
				),
			),
		),
		active: (
			custom-control-label: (
				before: (
					background-color: $white,
					border-color: $primary-d2,
					box-shadow: $c-unset,
					color: $component-active-color,
				),
				after: (
					background-color: rgba($primary, 0.1),
				),
			),
		),
		disabled: (
			custom-control-label: (
				color: $c-unset,
				cursor: $disabled-cursor,
				before: (
					background-color: $white,
					border-color: $primary-l1,
					box-shadow: none,
				),
				after: (
					content: normal,
				),
			),
		),
		checked: (
			custom-control-label: (
				before: (
					background-color: $primary,
					border-color: $primary,
					box-shadow: $c-unset,
					color: $white,
				),
			),
			hover: (
				custom-control-label: (
					before: (
						background-color: $primary-d1,
						border-color: transparent,
					),
				),
			),
			active: (
				custom-control-label: (
					before: (
						background-color: $primary-d2,
						border-color: transparent,
					),
				),
			),
			disabled: (
				cursor: $disabled-cursor,
				custom-control-label: (
					before: (
						background-color: $white,
						box-shadow: none,
					),
				),
			),
		),
	),
	$custom-control-input
);

// Custom Control Primary

$custom-control-primary-label-text: () !default;
$custom-control-primary-label-text: map-deep-merge(
	(
		font-weight: $font-weight-bold,
	),
	$custom-control-primary-label-text
);

// .custom-checkbox

$custom-checkbox: () !default;
$custom-checkbox: map-deep-merge(
	(
		custom-control-input: (
			custom-control-label: (
				before: (
					border-radius: clay-enable-rounded(0.125rem),
				),
			),
			checked: (
				custom-control-label: (
					before: (
						background-image: clay-icon(check-small, $white),
						background-size: 100% 100%,
					),
					after: (
						background-image: $c-unset,
						background-size: $c-unset,
					),
				),
				disabled: (
					custom-control-label: (
						before: (
							background-color: $primary-l1,
							border-color: $primary-l1,
						),
					),
				),
			),
			indeterminate: (
				custom-control-label: (
					before: (
						background-color: $component-active-bg,
						background-image: clay-icon(hr, $white),
						background-size: 100% 100%,
						border-color: $primary-d2,
						box-shadow: $c-unset,
					),
					after: (
						background-image: $c-unset,
						background-size: $c-unset,
					),
				),
				hover: (
					custom-control-label: (
						before: (
							background-color: $primary-d1,
							border-color: transparent,
						),
					),
				),
				active: (
					custom-control-label: (
						before: (
							background-color: $primary-d2,
							border-color: transparent,
						),
					),
				),
				disabled: (
					custom-control-label: (
						before: (
							background-color: $primary-l1,
							border-color: $primary-l1,
						),
					),
				),
			),
		),
	),
	$custom-checkbox
);

// .custom-radio

$custom-radio: () !default;
$custom-radio: map-deep-merge(
	(
		custom-control-input: (
			custom-control-label: (
				before: (
					border-radius: 50%,
				),
			),
			disabled: (
				custom-control-label: (
					before: (
						border-color: $primary-l1,
					),
				),
			),
			checked: (
				custom-control-label: (
					before: (
						background-image: clay-icon(circle, $white),
						background-position: center,
						background-repeat: no-repeat,
						background-size: 8px 8px,
					),
					after: (
						background-image: $c-unset,
						background-size: $c-unset,
					),
				),
				disabled: (
					custom-control-label: (
						before: (
							background-color: $primary-l1,
							border-color: $primary-l1,
						),
					),
				),
			),
		),
	),
	$custom-radio
);

// .toggle-switch

// must all be same units--start
$toggle-switch-bar-padding: 4px !default; // space between button and bar, can be negative value
$toggle-switch-bar-padding-mobile: 4px !default;

$toggle-switch-button-width: 24px !default;
$toggle-switch-button-width-mobile: 24px !default;

$toggle-switch-bar-height: ($toggle-switch-bar-padding * 2) +
	$toggle-switch-button-width !default;
$toggle-switch-bar-height-mobile: ($toggle-switch-bar-padding-mobile * 2) +
	$toggle-switch-button-width-mobile !default;

$toggle-switch-bar-width: 56px !default; // width of switch bar
$toggle-switch-bar-width-mobile: 56px !default; // width of switch bar
// must all be same units--end

$toggle-switch-bar-bg: $gray-400 !default;
$toggle-switch-bar-border-color: $toggle-switch-bar-bg !default;
$toggle-switch-bar-border-radius: 20px !default;
$toggle-switch-bar-border-width: 1px !default;
$toggle-switch-bar-font-size: 0.625rem !default;
$toggle-switch-bar-font-size-mobile: 0.625rem !default;
$toggle-switch-bar-icon-color: $white !default;
$toggle-switch-bar-focus-box-shadow: $component-focus-box-shadow !default;

$toggle-switch-button-bg: $white !default;
$toggle-switch-button-border-color: $toggle-switch-button-bg !default;
$toggle-switch-button-border-radius: 50% !default;
$toggle-switch-button-border-width: 1px !default;
$toggle-switch-button-font-size: $toggle-switch-bar-font-size !default; // 12px
$toggle-switch-button-font-size-mobile: $toggle-switch-bar-font-size-mobile !default; // 10px
$toggle-switch-button-icon-color: $gray-900 !default;

// Toggle Switch On

$toggle-switch-bar-on-bg: $component-active-bg !default;
$toggle-switch-bar-on-border-color: $toggle-switch-bar-on-bg !default;
$toggle-switch-bar-on-icon-color: $white !default;

$toggle-switch-button-on-bg: $white !default;
$toggle-switch-button-on-border-color: $white !default;
$toggle-switch-button-on-border-radius: $toggle-switch-button-border-radius !default;
$toggle-switch-button-on-icon-color: $primary !default;

// .sticker

$sticker: () !default;
$sticker: map-deep-merge(
	(
		color: $black,
		font-size: 0.875rem,
		font-weight: $font-weight-bold,
	),
	$sticker
);

$sticker-sizes: () !default;
$sticker-sizes: map-deep-merge(
	(
		sticker-sm: (
			font-size: 0.6875rem,
		),
		sticker-lg: (
			font-size: 0.875rem,
		),
		sticker-xl: (
			font-size: 1.125rem,
		),
		sticker-2xl: (
			height: 4.5rem,
			font-size: 1.5rem,
			line-height: 4.5rem,
			width: 4.5rem,
		),
	),
	$sticker-sizes
);

// Sticker Positions

$sticker-inside-offset: 1rem !default; // 16px

// .sticker-bottom-left

$sticker-bottom-left: () !default;
$sticker-bottom-left: map-merge(
	(
		bottom: $sticker-inside-offset,
		left: $sticker-inside-offset,
		position: absolute,
		right: auto,
		top: auto,
	),
	$sticker-bottom-left
);

// .sticker-bottom-right

$sticker-bottom-right: () !default;
$sticker-bottom-right: map-merge(
	(
		bottom: $sticker-inside-offset,
		left: auto,
		position: absolute,
		right: $sticker-inside-offset,
		top: auto,
	),
	$sticker-bottom-right
);

// .sticker-top-left

$sticker-top-left: () !default;
$sticker-top-left: map-merge(
	(
		left: $sticker-inside-offset,
		position: absolute,
		top: $sticker-inside-offset,
	),
	$sticker-top-left
);

// .sticker-top-right

$sticker-top-right: () !default;
$sticker-top-right: map-merge(
	(
		left: auto,
		position: absolute,
		right: $sticker-inside-offset,
		top: $sticker-inside-offset,
	),
	$sticker-top-right
);

// .sticker-user-icon

$sticker-user-icon: () !default;
$sticker-user-icon: map-deep-merge(
	(
		box-shadow: $c-unset,
	),
	$sticker-user-icon
);

// .sticker-primary

$sticker-primary: () !default;
$sticker-primary: map-deep-merge(
	(
		background-color: $primary,
		color: $white,
	),
	$sticker-primary
);

// .sticker-secondary

$sticker-secondary: () !default;
$sticker-secondary: map-deep-merge(
	(
		background-color: $secondary,
		color: $white,
	),
	$sticker-secondary
);

// .sticker-info

$sticker-info: () !default;
$sticker-info: map-deep-merge(
	(
		background-color: $info,
		color: $white,
	),
	$sticker-info
);

// .sticker-success

$sticker-success: () !default;
$sticker-success: map-deep-merge(
	(
		background-color: $success,
		color: $white,
	),
	$sticker-success
);

// .sticker-warning

$sticker-warning: () !default;
$sticker-warning: map-deep-merge(
	(
		background-color: $warning,
		color: $white,
	),
	$sticker-warning
);

// .sticker-danger

$sticker-danger: () !default;
$sticker-danger: map-deep-merge(
	(
		background-color: $accent-1,
		color: $white,
	),
	$sticker-danger
);

// .sticker-light

$sticker-light: () !default;
$sticker-light: map-deep-merge(
	(
		background-color: $light,
		color: color-yiq($light),
	),
	$sticker-light
);

// .sticker-dark

$sticker-dark: () !default;
$sticker-dark: map-deep-merge(
	(
		background-color: $dark,
		color: $white,
	),
	$sticker-dark
);

// .c-kbd-group

$c-kbd-group: () !default;
$c-kbd-group: map-deep-merge(
	(
		font-size: 1rem,
	),
	$c-kbd-group
);

// .c-kbd

$c-kbd: () !default;
$c-kbd: map-deep-merge(
	(
		background-color: transparent,
		border-radius: 2px,
		border-color: transparent,
		border-style: solid,
		border-width: 1px,
		box-shadow: none,
		color: inherit,
		display: inline-block,
		font-family: $font-family-sans-serif,
		font-size: map-get($c-kbd-group, font-size),
		font-weight: $font-weight-semi-bold,
		height: 1.25rem,
		line-height: 1.125,
		min-width: 1.25rem,
		padding-bottom: 0,
		padding-left: 0.15625rem,
		padding-right: 0.15625rem,
		padding-top: 0,
		text-align: center,
		text-transform: capitalize,
	),
	$c-kbd
);

// .c-kbd > .c-kbd

$c-kbd-c-kbd: () !default;
$c-kbd-c-kbd: map-merge(
	(
		border-width: 0,
		font-size: inherit,
		font-weight: inherit,
		height: auto,
		line-height: inherit,
		min-width: 0,
		padding: 0,
	),
	$c-kbd-c-kbd
);

// .c-kbd-monospaced

$c-kbd-monospaced: () !default;
$c-kbd-monospaced: map-deep-merge(
	(
		padding: 0,
	),
	$c-kbd-monospaced
);

// .c-kbd-inline

$c-kbd-inline: () !default;
$c-kbd-inline: map-deep-merge(
	(
		border-width: 0,
		font-weight: $font-weight-light,
		height: auto,
		line-height: inherit,
		min-width: 0,
		padding: 0,
	),
	$c-kbd-inline
);

// Kbd Separator

// .c-kbd > .c-kbd-separator

$c-kbd-c-kbd-separator: () !default;
$c-kbd-c-kbd-separator: map-merge(
	(
		font-weight: $font-weight-normal,
	),
	$c-kbd-c-kbd-separator
);

// .c-kbd-inline > .c-kbd-separator

$c-kbd-inline-c-kbd-separator: () !default;
$c-kbd-inline-c-kbd-separator: map-merge(
	(
		font-weight: inherit,
	),
	$c-kbd-inline-c-kbd-separator
);

// Kbd Sizes

// .c-kbd-group-sm

$c-kbd-group-sm: () !default;
$c-kbd-group-sm: map-deep-merge(
	(
		font-size: 0.75rem,
	),
	$c-kbd-group-sm
);

// .c-kbd-sm

$c-kbd-sm: () !default;
$c-kbd-sm: map-deep-merge(
	(
		font-size: map-get($c-kbd-group-sm, font-size),
		height: 1rem,
		line-height: calc(1rem - 2px),
		min-width: 1rem,
	),
	$c-kbd-sm
);

// .c-kbd-group-lg

$c-kbd-group-lg: () !default;
$c-kbd-group-lg: map-deep-merge(
	(
		font-size: 1.25rem,
	),
	$c-kbd-group-lg
);

// .c-kbd-lg

$c-kbd-lg: () !default;
$c-kbd-lg: map-deep-merge(
	(
		font-size: map-get($c-kbd-group-lg, font-size),
		height: 1.5rem,
		line-height: calc(1.5rem - 2px),
		min-width: 1.5rem,
	),
	$c-kbd-lg
);

// Kbd Variants

// .c-kbd-group-light

$c-kbd-group-light: () !default;
$c-kbd-group-light: map-deep-merge(
	(
		color: $gray-700,
	),
	$c-kbd-group-light
);

// .c-kbd-light

$c-kbd-light: () !default;
$c-kbd-light: map-deep-merge(
	(
		background-color: $gray-100,
		border-color: $gray-600,
		color: $gray-700,
	),
	$c-kbd-light
);

$c-kbd-group-dark: () !default;
$c-kbd-group-dark: map-deep-merge(
	(
		color: $white,
	),
	$c-kbd-group-dark
);

$c-kbd-dark: () !default;
$c-kbd-dark: map-deep-merge(
	(
		background-color: $dark-d2,
		border-color: $dark-d2,
		color: $white,
	),
	$c-kbd-dark
);

// table element

$c-table-base: () !default;
$c-table-base: map-merge(
	(
		border-collapse: collapse,
	),
	$c-table-base
);

// th

$c-th-base: () !default;
$c-th-base: map-merge(
	(
		height: 56px,
		text-align: left,
	),
	$c-th-base
);

// .table-responsive

$table-responsive-margin-bottom: 1.5rem !default;

// .table

$table-bg: null !default;
$table-border-color: $gray-100 !default;
$table-border-width: 0 !default;
$table-color: $black !default;
$table-font-size: 1rem !default;
$table-margin-bottom: 0 !default;

// tr.table-hover

$table-hover-bg: $primary-l3 !default;
$table-hover-color: $black !default;

// tr.table-active

$table-active-bg: $primary-l2 !default;

// tr.table-disabled

$table-disabled-bg: $white !default;
$table-disabled-color: #acacac !default;
$table-disabled-cursor: $disabled-cursor !default;
$table-disabled-pointer-events: none !default;

// .table-striped

$table-accent-bg: $brand-primary-l5 !default;

$table-striped-order: odd !default;

$table-head-color: $gray-800 !default;
$table-head-font-size: 0.875rem !default;
$table-head-font-weight: $font-weight-bold !default;
$table-head-height: 56px !default;

// .table-list

$table-list-border-color: $gray-100 !default;
$table-list-border-x-width: 0 !default;
$table-list-border-y-width: 0 !default;

$c-table-list: () !default;
$c-table-list: map-merge(
	(
		border-collapse: separate,
		border-style: solid,
		border-width: $table-list-border-y-width $table-list-border-x-width,
		border-radius: 8px,
		color: $black,
		font-size: 1rem,
		margin-bottom: $table-list-border-y-width,
		margin-top: $c-unset,
	),
	$c-table-list
);

$table-list-accent-bg: $gray-100 !default;
$table-list-hover-bg: clay-lighten($component-active-bg, 44.9) !default;
$table-list-active-bg: clay-lighten($component-active-bg, 44.9) !default;

$table-list-disabled-color: $gray-500 !default;

$table-list-divider-padding-x: 0.75rem !default; // 12px
$table-list-divider-padding-y: 0.4375rem !default; // 7px

// .table-list thead

$table-list-head-bg: $white !default;
$table-list-head-font-size: 0.875rem !default;
$table-list-head-font-weight: $font-weight-bold !default;
$table-list-head-height: 56px !default;
$table-list-head-vertical-alignment: middle !default;

// .table-list thead th a

$table-list-head-link: () !default;
$table-list-head-link: map-deep-merge((
	color: inherit,
	hover: (
		color: $black,
	),
), $table-list-head-link);

// Table List Title

$table-list-title: () !default;
$table-list-title: map-deep-merge(
	(
		color: $c-unset,
		font-size: $c-unset,
		line-height: 1.45,
	),
	$table-list-title
);

$table-list-title-link: () !default;
$table-list-title-link: map-deep-merge(
	(
		border-radius: 1px,
		color: $c-unset,
		display: inline-block,
		max-width: 100%,
		transition: box-shadow 0.15s ease-in-out,
		hover-color: $gray-900,
		focus-box-shadow: $component-focus-box-shadow,
		focus-outline: 0,
	),
	$table-list-title-link
);

// Table List Link

$table-list-link: () !default;
$table-list-link: map-deep-merge(
	(
		border-radius: 1px,
		color: $gray-900,
		display: inline-block,
		max-width: 100%,
		transition: box-shadow 0.15s ease-in-out,
		hover-color: $gray-900,
		focus-box-shadow: $component-focus-box-shadow,
		focus-outline: 0,
	),
	$table-list-link
);

// Table List Action Link

$table-list-action-link: () !default;
$table-list-action-link: map-deep-merge(
	(
		color: $gray-600,
		font-size: 1rem,
		transition: #{color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out},
		hover-bg: rgba(0, 0, 0, 0.02),
		hover-color: $gray-900,
		focus-bg: rgba(0, 0, 0, 0.02),
		focus-color: $gray-900,
		focus-box-shadow: $component-focus-box-shadow,
		focus-outline: 0,
		active-bg: rgba(0, 0, 0, 0.04),
	),
	$table-list-action-link
);

